.body {
    background: #250630 url(../img/main-stars.png) no-repeat center / cover;
}

.page {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow-y: auto;
}

.page__border {
    position: absolute;
    border: 4px solid rgba(101, 59, 148, 0.8);
    border-radius: 24px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-height: 800px;
    height: calc(100% - 40px);
    width: calc(100% - 160px);
    max-width: 1440px;
    overflow: hidden;
}

.page-main-bg {
    background: url(../img/main-bg.jpg) no-repeat center / cover;
    &.page-main-dark {
        background: radial-gradient(62.57% 62.57% at 50% 50%, rgba(0, 0, 0, 0) 15.72%, #000000 100%), url(../img/main-bg.jpg) no-repeat center / cover;
    }
}

.main-logo {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.bottom-btn {
    position: absolute;
    left: 50%;
    bottom: 48px;
    transform: translateX(-50%);
    z-index: 200;
    &:after {
        content: "";
        width: 326px;
        height: 326px;
        border-radius: 50%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: -222px;
        background: #4617A4;
        z-index: 1;
        filter: blur(105px);
    }
    .btn {
        z-index: 10;
    }
}

[fade-in-preloader] {
    display: none;
}